import './CSSfiles/Home.css';
import './App.css';
import './CSSfiles/Main.css';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Layout from './components/Layout.js';
import Login from './components/Login.js';
import Signup from './components/Signup';
import Home from './components/Home.js';
import Main from './components/Main';
import PhoneSignUp from './components/PhoneSignUp';
import { UserAuthContextProvider } from "./context/UserAuthContext";
function App() {
  return (
    <div className="App">
      <BrowserRouter>
      <UserAuthContextProvider>
      <Routes>
        <Route path="/gift-hamper-co" element={<Layout />}>
          <Route index element={<Home />} />
          <Route path="/gift-hamper-co/Login" element={<Login />} />
          <Route path="/gift-hamper-co/Register" element={<Signup />} />
          <Route path='/gift-hamper-co/Main' element={<Main/>} />
          <Route path='/gift-hamper-co/PhoneSignUp' element={<PhoneSignUp />} />
          
        </Route>
      </Routes>
      </UserAuthContextProvider>
    </BrowserRouter>
    </div>
  );
}

export default App;
